
#EighthSection {
    display: grid;
    justify-items: center;
    padding-top: 100px;
    padding-bottom: 100px;
    background-color: #F4F4F4;
}

.eighth-section-container {
    width: 90%;
    display: grid;
    row-gap: 20px;
    justify-items: center;
}

.eighth-section-text {
    justify-self: start;
    align-self: start;
}

.eighth-section-title {
    text-align: center;
	font-family: 'ubuntu';
	font-weight: bold;
	font-size: 57px;
}
.eighth-section-description {
	font-family: 'open sans';
	font-size: 18px;
	line-height: 1.7em;
	margin-bottom: 30px;
}

