
.side-navigation-background {
    display: none;
    position: fixed;
    z-index: 1;
    width: 100%;
    height: 120vh;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 50%;
}

.fade {
    opacity: 0%;
    transition: all 0.6s ease;
}

.side-menu {
    z-index: 2;
    display: none;
    position: fixed;
    width: 40%;
    height: 105vh;
    margin: 0;
    padding: 0;
    padding-right: 0%;
    margin-left: 100%;
    background-color: #f4f4f4;
    grid-template-rows: 1fr 6fr 1fr 1fr 1fr;
    grid-template-columns: 1fr;
    justify-items: end;
}

.side-navigation {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(5, 1fr);
    justify-items: end;
    grid-gap: 15px;
    font-family: 'open sans';
    color: gray; 
}

.side-navigation > li > a {
    font-size: 18px;
    color: rgb(0, 0, 0);
    float: right;
    padding-right: 20px;
}


.side-menu__close-button{
    width: 30px;
    height: 30px;
    margin: 15px 20px 0px 0px;
    align-self: start;
    border-radius: 5px;
    background: rgb(131, 131, 131);
}

.side-menu__close-button:focus {
    background: rgb(173, 173, 173);
}

.side-menu-logo {
    background-size:100% 100%;
    width: 30px;
    height: 30px;
}

.side-menu-li {
    margin: 0;
    padding: 0;
    width: 100%;
    border-bottom: solid black;
    align-self: start;
}

.side-menu__media-links {
    display: grid;
    grid-template-rows: auto;
    row-gap: 20px;
    margin-right: 20px;
}


