
.modal-window {
    display: none;
    opacity: 0;
    position: fixed;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    transition: all 0.5s ease;
}

.modal-bar {
    display: flex;
    flex-direction: column;
    margin: 10% auto;
    max-width: 600px;
    padding-bottom: 50px;
    border:solid rgb(100, 100, 100) 2px;
    border-radius: 8px;
    background-color: #F4F4F4;
    opacity: 100%;
}

.modal-bar-content {
    display: grid;
    width: 90%;
    margin: 0 auto;
}

.modal_opened {
    display: block;
}

.close-btn {
    width: 30px;
    height: 30px;
    margin: 5px;
    align-self: flex-end;
    border-radius: 5px;
    background: rgb(131, 131, 131);
}
  
.close-btn:focus {
    background: rgb(173, 173, 173);
}
  
.close-btn:hover {
    background: rgb(100, 100, 100);
}
  