
body, header, section, h1, h2, h3, nav, ul, li, p {
  margin: 0;
  padding: 0;
}

section .container {
  column-gap: 30px;
}

.section-title {
  font-family: 'ubuntu';
	font-weight: bold;
	font-size: 50px;
	margin-bottom: 30px;
}

.animated {
  width: 90%;
}

.nav-item-transition {
  transition: all 0.5s ease;
}

.shift-up {
  position: relative;
  transform: translateY(-8px);
}

.form-btn {
  display: block;
  width: 220px;
  padding: 24px 0px;
  background-color: #fc2020;
  border: none;
  text-decoration: none;
  font-weight: bold;
  color: #fff;
  text-align: center;
  box-sizing: border-box;
  transition: all 0.5s ease;
}

.form-btn:hover {
  cursor: pointer;
  background-color: #ff0f0f;
  box-shadow: 0 0 10px 10px #ff3434;
}

.form {
  display: grid;
  grid-template-rows: repeat(4, 1fr);
  justify-items: center;
  grid-gap: 10px;
  margin:0 auto;
}

.input {
  width: 340px;
  height: 35px;
  padding-top: 20px;
  outline: none;
  border: none;
  border-bottom:solid #bbbbbb 2px;
  border-radius: 2px;
  background-color: #F4F4F4;
}





